// DeviceNavigator.js
import React, { useState } from "react";

const DeviceNavigator = () => {
  const [ipInput, setIpInput] = useState("");
  const [webviewUrl, setWebviewUrl] = useState("");

  const openDeviceUI = () => {
    if (!ipInput.trim()) {
      alert("Please enter a valid IP address.");
      return;
    }

    let url = ipInput.trim();
    if (!url.startsWith("http://") && !url.startsWith("https://")) {
      url = "http://" + url;
    }
    setWebviewUrl(url);
  };

  return (
    <div>
      <div style={{ margin: "20px 0" }}>
        <input
          type="text"
          placeholder="Enter device IP"
          value={ipInput}
          onChange={(e) => setIpInput(e.target.value)}
          style={{ padding: "10px", width: "200px", marginRight: "10px" }}
        />
        <button onClick={openDeviceUI} style={{ padding: "10px 20px" }}>
          Go
        </button>
      </div>

      {webviewUrl && (
        <div
          className="w-full h-full bg-white rounded-xl overflow-hidden border shadow"
          style={{ marginTop: "30px" }}
        >
          <webview
            src={webviewUrl}
            style={{
              width: "1135px",
              height: "70vh",
              borderRadius: "12px",
              overflow: "hidden",
              marginLeft: "30px",
            }}
          />
        </div>
      )}
    </div>
  );
};

export default DeviceNavigator;

