// DeviceScanner.js
import React, { useState, useEffect } from "react";
import axios from "axios";

const DeviceScanner = () => {
  const [devices, setDevices] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const fetchDevices = async () => {
    setLoading(true);
    setError("");
    try {
      const res = await axios.get("http://localhost:3535/api/devices");
      setDevices(res.data);
    } catch (err) {
      console.error("Error fetching devices:", err);
      setError("Failed to fetch devices.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchDevices();
    const interval = setInterval(fetchDevices, 2000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div>
      <h2>Connected Devices on Local Network</h2>
      {error && <div style={{ color: "red" }}>{error}</div>}
      {devices.length === 0 && !loading && !error && <p>No devices found.</p>}

      {devices.length > 0 && (
        <table
          border="1"
          cellPadding="8"
          style={{ borderCollapse: "collapse", width: "100%", marginTop: "10px" }}
        >
          <thead style={{ backgroundColor: "#007BFF", color: "white" }}>
            <tr>
              <th>Device Name</th>
              <th>MAC Address</th>
              <th>IP Address</th>
              <th>Status</th>
              <th>Operation</th>
            </tr>
          </thead>
          <tbody style={{ color: "#007BFF" }}>
            {devices.map((device) => (
              <tr key={device.id}>
                <td>{device.name && device.name !== "?" ? device.name : device.ip}</td>
                <td>{device.mac}</td>
                <td>{device.ip}</td>
                <td>Active</td>
                <td>-</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default DeviceScanner;

